.shop-header {
  border-bottom: 2px solid rgba(95, 126, 95, 0.808);
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  background: #203127a8;
}

.shop-header a {
  color: rgb(158, 199, 142);
  text-decoration: none;
}
.shop-header a:hover {
  color: rgba(75, 255, 4, 0.411);
}

.shop-header .logo {
  font-family: 'Hanalei', cursive;
  font-size: 4rem;
  padding-left: 1rem;
}

.shop-header .shopping-cart {
  align-self: center;
  font-size: 1.3rem;
  padding-right: 1rem;
  text-align: center;
}

.shop-header .shopping-cart .cart-icon {
  font-size: 2.2rem;
  margin-right: 10px;
}


.show-item {
  display: flex;
  margin: 1vh;
  padding: 1vh;
  width: 34vh;
  border: 1px solid rgba(145, 240, 141, 0.247);
}

.show-item img {
  width: 150px;
  height: 143px;
  margin: 1vh;
}

.show-item .item-details {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-bottom: 10px;
  align-items: flex-start;
}

.show-item .item-details .item-title {
  font-size: 1.2rem;
  font-weight: bold;
  color: #fff;
}

.show-item .item-details .item-price {
  font-size: 1.4rem;
}



.item-list {
  list-style: none;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.shopping-cart-table {
  margin-top: 45px;
}

.shopping-cart-table .total {
  text-align: right;
  font-size: 1.3rem;
  margin-right: 10px;
}

.shopping-cart-table button {
  margin-left: 5px;
  margin-top: 5px;
}
.preview-item {
  display: flex;
  align-items: flex-start;
}
.preview-item .item-details-img {
  width: 30vh;
  height: auto;
  margin: 2vh 2.5vh;
}

.preview-item .item-details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.preview-item .item-details h4 {
  font-size: 30px;
}

.preview-item .item-details span {
  width: 50%;
}

.preview-item .item-details button {
  width: 22vh;
  margin: 1vh 0;
}


body {
    background: url(/Game-store/static/media/bg2.e2d95412.jpg) no-repeat fixed center / cover;
    color: #fff;
    margin: 0;
    padding: 0;
}
